import PropTypes from "prop-types";
import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { animateScroll } from "react-scroll";
import FooterNewsletter from "../../components/footer/FooterNewsletter";

const FooterOne = ({
  backgroundColorClass,
  spaceTopClass,
  spaceBottomClass,
  spaceLeftClass,
  spaceRightClass,
  containerClass,
  extraFooterClass,
  sideMenu,
}) => {
  const [scroll, setScroll] = useState(0);
  const [top, setTop] = useState(0);

  useEffect(() => {
    setTop(100);
    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  const scrollToTop = () => {
    animateScroll.scrollToTop();
  };

  const handleScroll = () => {
    setScroll(window.scrollY);
  };

  return (
    <footer
      className="footer-area "
      // className={`footer-area ${
      //   backgroundColorClass ? backgroundColorClass : ""
      // } ${spaceTopClass ? spaceTopClass : ""} ${
      //   spaceBottomClass ? spaceBottomClass : ""
      // } ${extraFooterClass ? extraFooterClass : ""} ${
      //   spaceLeftClass ? spaceLeftClass : ""
      // } ${spaceRightClass ? spaceRightClass : ""}`}
    >
      <div className={`${containerClass ? containerClass : "container"}`}>
        <div className="row">
          <div className={`${sideMenu ? "col-xl-4 col-sm-4" : "col-lg-4"}`}>
            <div className="widget widget-about footer-widget">
              <div className="footer-title">
                <h3> CONTACT US</h3>
              </div>

              <ul className="widget-list">
                <li>
                  {" "}
                  <i className="pe-7s-map-marker pe-5x pe-va"></i>
                  <span>
                    403-Fourth Floor, Fortune Ambience Business Space, South
                    Tukoganj, Indore, Madhya Pradesh 452001s
                  </span>
                </li>

                <li>
                  <a href="#">
                    {" "}
                    <i className="pe-7s-call"></i>
                    <span>0731-3589090 </span>
                  </a>
                </li>
                <li>
                  <a href="#">
                    {" "}
                    <i className="pe-7s-mail-open"></i>{" "}
                    <span>info@impetrosys.com</span>
                  </a>
                </li>
              </ul>
            </div>

            {/* footer copyright */}
            {/* <FooterCopyright
              footerLogo="/assets/img/logo/logo.png"
              spaceBottomClass="mb-30"
            /> */}
          </div>
          <div className={`${sideMenu ? "col-xl-2 col-sm-4" : "col-lg-2 "}`}>
            <div className="footer-widget mb-30 ml-30">
              <div className="footer-title">
                <h3>Information</h3>
              </div>
              <div className="footer-list">
                <ul>
                  <li>
                    <Link to={process.env.PUBLIC_URL + "/about"}>About us</Link>
                  </li>
                  <li>
                    <Link to={process.env.PUBLIC_URL + "#/"}>
                      Store location
                    </Link>
                  </li>
                  <li>
                    <Link to={process.env.PUBLIC_URL + "/contact"}>
                      Contact
                    </Link>
                  </li>
                  <li>
                    <Link to={process.env.PUBLIC_URL + "#/"}>
                      Orders tracking
                    </Link>
                  </li>
                </ul>
              </div>
            </div>
          </div>
          <div className={`${sideMenu ? "col-xl-2 col-sm-4" : "col-lg-2"}`}>
            <div
              className={`${
                sideMenu
                  ? "footer-widget mb-30 ml-95"
                  : "footer-widget mb-30 ml-50"
              }`}
            >
              <div className="footer-title">
                <h3>Customer Service</h3>
              </div>
              <div className="footer-list">
                <ul>
                  <li>
                    <Link to={process.env.PUBLIC_URL + "#/"}>Returns</Link>
                  </li>
                  <li>
                    <Link to={process.env.PUBLIC_URL + "#/"}>
                      Support Policy
                    </Link>
                  </li>
                  <li>
                    <Link to={process.env.PUBLIC_URL + "#/"}>Size guide</Link>
                  </li>
                  <li>
                    <Link to={process.env.PUBLIC_URL + "#/"}>FAQs</Link>
                  </li>
                </ul>
              </div>
            </div>
          </div>
          <div className={`${sideMenu ? "col-xl-3 col-sm-4" : "col-lg-4"}`}>
            {/* footer newsletter */}
            <FooterNewsletter
              spaceBottomClass="mb-30"
              spaceLeftClass="ml-70"
              sideMenu={sideMenu}
            />
          </div>
        </div>
      </div>
      <div className={`${containerClass ? containerClass : "container"}`}>
        <div className="row">
          <div
            className={`${
              sideMenu ? "col-xl-12 col-sm-12" : "col-lg-12 col-sm-12"
            }`}
          >
            <p className="text-center pb-30 pt-30">
              © 2020{" "}
              <a
                href="https://impetrosys.com/"
                rel="noopener noreferrer"
                target="_blank"
              >
                Impetrosys All Rights Reserved
              </a>
            </p>
            {/* <FooterCopyright
              footerLogo="/assets/img/logo/logo.png"
              spaceBottomClass="mb-30"
            /> */}
          </div>
          <div
            className={`${
              sideMenu ? "col-xl-12 col-sm-12" : "col-lg-12 col-sm-12"
            }`}
          >
            <div className={`${sideMenu ? "footer-widget" : "footer-widget"}`}>
              <div className="footer-list mb-20">
                <ul className="footer-list-link">
                  <li className="footer-list-linnks">
                    <a
                      href="//www.facebook.com"
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      <i className="fa fa-facebook" aria-hidden="true"></i>
                    </a>
                  </li>
                  <li className="footer-list-linnks">
                    <a
                      href="//www.twitter.com"
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      <i className="fa fa-twitter" aria-hidden="true"></i>
                    </a>
                  </li>
                  <li className="footer-list-linnks">
                    <a
                      href="//www.instagram.com"
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      <i className="fa fa-instagram" aria-hidden="true"></i>
                    </a>
                  </li>
                  <li className="footer-list-linnks">
                    <a
                      href="//www.youtube.com"
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      <i className="fa fa-youtube-play" aria-hidden="true"></i>
                    </a>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>

      <button
        className={`scroll-top ${scroll > top ? "show" : ""}`}
        onClick={() => scrollToTop()}
      >
        <i className="fa fa-angle-double-up"></i>
      </button>
    </footer>
  );
};

FooterOne.propTypes = {
  backgroundColorClass: PropTypes.string,
  containerClass: PropTypes.string,
  extraFooterClass: PropTypes.string,
  sideMenu: PropTypes.bool,
  spaceBottomClass: PropTypes.string,
  spaceTopClass: PropTypes.string,
  spaceLeftClass: PropTypes.string,
  spaceRightClass: PropTypes.string,
};

export default FooterOne;
